@import url('https://fonts.googleapis.com/css2?family=Baloo+Tamma+2:wght@400;500;700&display=swap');
body{
  font-family: 'Baloo Tamma 2', cursive;
}
.wrapper-main{
  padding: 60px;
}
.wrapper-main .Title{
    color: #a3d2ca;
    font-size: 30px;
}
.wrapper-main .Linea{
 background-color: #a3d2ca;
 border: none;
 height: 3px;
}
.wrapper-main .footer{
  text-align: center;
}
.titulo-Categoria{
  color:#a3d2ca;
  font-weight: 700;
  font-size: 25px;
}

.wrapper-form .input-categoria{
  color:gray;
  font-size: 1.2rem;
  padding: 10px;
  width: 100%;
}
.wrapper-card-gift-item{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
}
.wrapper-gift-item{
  margin-bottom: 15px;
  overflow: hidden;
  box-shadow: 7px 7px #5eaaa8;
  border: 1px solid #5eaaa8;
  border-radius: 10px;
  max-width: 360px;
}
.wrapper-gift-item  .gift{
  box-sizing: border-box;
  border-radius: 10px;
  padding-top:15px;
  padding-bottom:0px;
  padding-right: 15px;
  padding-left:  15px;
  width: 100%;
  height: 70%;
}

.wrapper-gift-item .titulo-gift{
  text-align: center;
  font-weight: 500;
  font-size: 18px;
  color:#000000;
  margin: 10px;
}
@media only screen and (max-width: 700px)
{
  .wrapper-main .Title{
    text-align: center;
    color: #a3d2ca;
    font-size: 30px;
}
.wrapper-form .input-categoria{
  color:gray;
  font-size: 18px;
  padding: 10px;
  border: 2px solid #5eaaa8;
  width: 100%;
}
  .wrapper-card-gift-item{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-left: -30px;
  }
  .titulo-Categoria{
    color:#a3d2ca;
    font-weight: 700;
    font-size: 25px;
  }
  .wrapper-gift-item .titulo-gift{
    font-weight: 500;
    font-size: 14px;
    color:#000000;
    margin: 10px;
  }
}